// Imports
import * as React from 'react'
import { Link } from 'gatsby'
import { Helmet } from "react-helmet";
import favicon from '../images/favicon.ico'
import { StaticImage } from 'gatsby-plugin-image'

// Define component
const IndexPage = () => {
  return (
    <body>
      <Helmet>
        <title>Team 3602</title>
        <meta charset="UTF-8"></meta>
        <link rel="icon" href={favicon}/>
      </Helmet>
      <header class="header flex">
        <div>
        <StaticImage alt="RobomosLogo" src="../images/logo.png" class="logo"/>
        </div>
        <nav>
          <ul id="navigation" class="navigation flex">
            <li class="active">
              <Link to="/">Home</Link>
            </li>
            <li class="active">
              <Link to="first">First</Link>
            </li>
            <li class="active">
              <Link to="sponsors">Sponsors</Link>
            </li>
            <li class="active">
              <Link to="members">Members</Link>
            </li>
            <li class="active">
              <Link to="contact">Contact</Link>
            </li>
            <li class="active">
              <Link to="about">About</Link>
            </li>
          </ul>
        </nav>
      </header>
      <main class="body">
        Stuff
      </main>
      <footer class="footer">
        <p>Copyright (C) 2022, Team 3602.</p>
      </footer>
    </body>
  )
}

// Export component
export default IndexPage
